
#app{min-width:1450px;font-family:Arial,Helvetica
}
#app .page1{height:700px;border:1px solid black;background:url("../static/bj.png") no-repeat;background-size:cover;margin-bottom:180px
}
#app .page1 .top{color:white;font-style:normal
}
#app .page1 .top img{display:inline-block;margin-left:200px;vertical-align:-16px
}
#app .page1 .top .text{display:inline-block
}
#app .page1 .top .text p{margin-top:18px;margin-bottom:2px;font-size:26px;background:url("assets/dingbuxian.png") no-repeat 0 29px
}
#app .page1 .top .text span{font-size:16px
}
#app .page1 .context img.left{width:365px;margin-left:244px;margin-top:126px;vertical-align:-2px
}
#app .page1 .context .right{width:648px;margin-left:145px;display:inline-block
}
#app .page1 .context .right img{margin-left:32px
}
#app .page1 .context .right p{width:612px;height:154px;font-size:20px;color:white;line-height:34px;border-bottom:2px solid white;padding:50px 20px 20px 20px;margin-top:-37px;background:url("assets/xiankuang.png") no-repeat;background-size:cover
}
#app .page1 .context .right .btn{display:inline-block;width:180px;height:62px;border-radius:50px;line-height:62px;text-align:right;padding-right:25px;background-color:rgba(255,255,255,0.5);font-size:16px;color:white;border:1px solid rgba(255,255,255,0);margin-top:58px
}
#app .page1 .context .right .btn.iphone{background:url("assets/apple.png") no-repeat;background-position:27px 11px;background-color:rgba(255,255,255,0.5);margin-left:78px
}
#app .page1 .context .right .btn.androd{background:url("assets/android.png") no-repeat;background-position:27px 11px;background-color:rgba(255,255,255,0.5);margin-left:88px
}
#app .page1 .context .right .btn:hover{border:1px solid #fff;background-color:#ffda26
}
#app .page1 .context .right .hl{margin-left:102px
}
#app .page1 .context .right .hr{margin-left:142px
}
#app .page2{height:510px
}
#app .page2 .center{width:965px;margin:0 auto
}
