}
#app .page2 .right{width:432px;margin-left:144px;display:inline-block
}
#app .page2 .right .name{font-size:26px;color:#956adc;border-bottom:1px solid #e7e7e7;width:158px;margin:0
}
#app .page2 .right .describe{font-size:20px;color:#353535;margin-top:10px
}
#app .page2 .right .text{font-size:16px;color:#6d6d6d
}
#app .page3{height:670px;overflow:hidden;background:url("assets/disanping-sanjiao.png") no-repeat 30px 0;background-size:1264px;background-position:right
}
#app .page3 .center{width:1004px;height:410px;background-color:white;padding-left:156px;margin:0 auto;margin-top:164px;box-shadow:0 2px 17px rgba(221,196,242,0.5)
}
#app .page3 .center img{width:270px;margin-top:-88px;vertical-align:-110px
}
#app .page3 .center .right{width:432px;margin-left:144px;display:inline-block
}
#app .page3 .center .right .name{font-size:26px;color:#956adc;border-bottom:1px solid #e7e7e7;width:158px;margin:0
}
#app .page3 .center .right .describe{font-size:20px;color:#353535;margin-top:10px
}
#app .page3 .center .right .text{font-size:16px;color:#6d6d6d
}
#app .page4{height:760px;position:relative
}
#app .page4 .left,#app .page4 .right{position:absolute
}
