}
#app .page4 .right{right:0
}
#app .page4 .center{text-align:center;width:283px;margin:0 auto
}
#app .page4 .center .name{font-size:26px;color:#956adc;border-bottom:1px solid #e7e7e7;width:212px;margin:0 auto;margin-top:50px;text-align:center
}
#app .page4 .center .describe{font-size:20px;color:#353535;margin-top:10px
}
#app .page4 .center .text{font-size:16px;color:#6d6d6d
}
#app .page4 .carousel{width:666px;height:480px;margin:0 auto
}
