}
#app .page5 .center .left .describe{font-size:20px;color:white;margin-top:10px
}
#app .page5 .center .left .text{font-size:16px;color:white;line-height:30px
}
#app .page5 .center img{display:inline-block;width:446px;margin-left:-21px;vertical-align:-155px
}
#app .bottom{height:88px;background:url("assets/zhandianditu.png") repeat;text-align:center;color:white;font-size:12px;padding-top:68px
}


